import React from 'react';
import { motion } from 'framer-motion';
//nothing to factor out becasue this component has a parent

function Event(props) {
  const months = [
    'January',
    'February',
    'March',
    'April',
    'May',
    'June',
    'July',
    'August',
    'September',
    'October',
    'November',
    'December',
  ];

  function formatTime() {
    const { minute } = props;
    if (minute.toString().length < 2) {
      return '0' + minute;
    } else {
      return minute;
    }
  }
  function formatColor() {
    const base = 'card mb-3 text-white ';
    const date = new Date();
    const eventDate = new Date(
      `${months[props.month]} ${props.day} ${props.year}`
    );
    if (
      parseInt(props.day) == date.toString().split(' ')[2] &&
      props.month == date.getMonth() &&
      props.year == date.getFullYear()
    ) {
      return base + 'bg-success';
    } else if (date < eventDate) {
      return base + 'bg-info';
    } else {
      return base + 'bg-danger';
    }
  }
  function formatYear() {
    const date = new Date();
    if (date.getFullYear() == props.year) {
      return ' ';
    } else {
      return props.year + ' ';
    }
  }
  function formatTOD() {
    if (props.tod == 1) return 'AM';
    if (props.tod == 2) return 'PM';
    return props.tod;
  }
  function formatDay() {
    if (props.day.toString() == new Date().toString().split(' ')[2]) {
      return 'Today!';
    }
    return `On ${months[props.month]} ${props.day} `;
  }
  return (
    <div className={formatColor()}>
      <div>
        <a className="float-right">
          {props.hour}:{formatTime()} {formatTOD()}&nbsp;
        </a>
        <br/>
        <a className="title">&nbsp; You have {props.title}</a>
  <a className="float-right"> {props.publicity==true ? "Public" : "Private"} &nbsp;</a>
        <br/>
        {}
        <a className="float-right">
          {formatDay()} {formatYear()}&nbsp;
        </a>
        <motion.button
          whileHover={{
            scale: 1.2,
          }}
          className="btn xxs btn-secondary py-0 px-1"
          onClick={props.onDelete}
        >
          {' '}
          <a>x</a>{' '}
        </motion.button>
      </div>
    </div>
  );
}
export default Event;
