import React, { useState, useEffect, useMemo } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import Event from './Event';
import { motion } from 'framer-motion';
import Friendevent from "./Friendevent"
const Home = (props) => {
  const [state, setState] = useState({
    username: props.username,
    events: props.events,
    month: props.month,
    friendEvents: props.friendEvents,
    months: [
      'January',
      'February',
      'March',
      'April',
      'May',
      'June',
      'July',
      'August',
      'September',
      'October',
      'November',
      'December',
    ],
    year: props.year,
  });

  useEffect(() => {
    setState({
      username: props.username,
      events: props.events,
      month: props.month,
      friendEvents: props.friendEvents,
      months: [
        'January',
        'February',
        'March',
        'April',
        'May',
        'June',
        'July',
        'August',
        'September',
        'October',
        'November',
        'December',
      ],
      year: props.year,
    });
  }, [props]);

  const highlightDate = (item) => {
    if (
      item+1 == new Date().toString().split(' ')[2] &&
      state.month == new Date().getMonth() &&
      state.year == new Date().getFullYear()
    ) {
      return 'days date highlight';
    } else {
      return 'days date';
    }
  };
  const variants = {
    hidden: { opacity: 0 },
    visible: { opacity: 1 },
  };
  const deleteEvent = (index) => {
    const cookie = localStorage.getItem('token');
    axios
      .delete(`https://calendar-app.systems/deleteevent/${index}`, {
        headers: { Authorization: `Bearer ${cookie}` },
      })
      .then(() => {

        let currentState = { ...state };
        currentState.events.splice(index, 1);
        setState(currentState);
        //currentState
        //rerender
      })
      .catch(() => {
        return
      });
  };

  const upMonth = () => {
    const statee = { ...state };
    if (state.month == 11) {
      statee.year += 1;
      statee.month = 0;
    } else {
      statee.month = (statee.month + 1) % 12;
    }
    setState(statee);
    document.getElementById('monthName').innerHTML = state.months[statee.month];
    document.getElementById('yearNum').innerHTML = state.year;
  };
  const downMonth = () => {
    const statee = { ...state };
    if (statee.month == 0) {
      statee.year -= 1;
      statee.month = 11;
    } else {
      statee.month = (statee.month - 1) % 12;
    }

    setState(statee);
    document.getElementById('monthName').innerHTML = state.months[state.month];
    document.getElementById('yearNum').innerHTML = state.year;
  };

  const daysInMonth = (month, year) => {
    switch (month) {
      case 0:
        return 31;
      case 1:
        if (year % 4 == 0) {
          return 29;
        } else {
          return 28;
        }
      case 2:
        return 31;
      case 3:
        return 30;
      case 4:
        return 31;
      case 5:
        return 30;
      case 6:
        return 31;
      case 7:
        return 31;
      case 8:
        return 30;
      case 9:
        return 31;
      case 10:
        return 30;
      case 11:
        return 31;
    }
  };

  const createCalendar = (month, year) => {
    const Start = new Date(state.months[month] + ' ' + year);
    const StartParts = Start.toString().split(' ');
    var date = [...Array(daysInMonth(month, year)).keys()];
    switch (StartParts[0].toLowerCase()) {
      case 'mon':
        break;
      case 'tue':
        date.unshift(-1);
        break;
      case 'wed':
        date.unshift(-1, -1);
        break;
      case 'thu':
        date.unshift(-1, -1, -1);
        break;
      case 'fri':
        date.unshift(-1, -1, -1, -1);
        break;
      case 'sat':
        date.unshift(-1, -1, -1, -1, -1);
        break;
      case 'sun':
        date.unshift(-1, -1, -1, -1, -1, -1);
        break;
    }
    return date;
  };
  return (
    <React.Fragment>
      <div className="month">
        <ul>
          <li>{state.username}</li>
          <br />
          <li className="prev">
            <motion.button
              onClick={() => downMonth()}
              className="btn btn-secondary"
              whileHover={{ scale: 1.1 }}
              whileTap={{ scale: 0.9 }}
            >
              &#10094;
            </motion.button>
          </li>
          <li className="next">
            <motion.button
              onClick={() => upMonth()}
              className="btn btn-secondary"
              whileHover={{ scale: 1.1 }}
              whileTap={{ scale: 0.9 }}
            >
              &#10095;
            </motion.button>
          </li>
          <li>
            <span id="monthName">{state.months[state.month]}</span>
            <br />
            <span id="yearNum">{state.year}</span>
          </li>
        </ul>
      </div>
      <ul className="weekdays">
        <li>Mo</li>
        <li>Tu</li>
        <li>We</li>
        <li>Th</li>
        <li>Fr</li>
        <li>Sa</li>
        <li>Su</li>
      </ul>
      <ul className="days">
        {createCalendar(state.month, state.year).map((item) => (
          <li>
            {item == -1 ? (
              <span className="wrong">{item == -1 ? '-' : item + 1}</span>
            ) : (
              <Link to={`/home/${1 + item}/${state.month}/${state.year}`}>
                <span className={highlightDate(item)}>
                  {item+1}
                </span>
              </Link>
            )}
          </li>
        ))}
      </ul>
      <br />
      <br />
      <br />
      <br />

      <motion.div
        transition={{ duration: 2 }}
        initial="hidden"
        animate="visible"
        variants={variants}
      >
        <div className="float ">
        {state.friendEvents &&
            state.friendEvents.map((event)=>(
              <Friendevent
                title={event.data.title}
                day={event.data.day}
                hour={event.data.hour}
                minute={event.data.minute}
                tod={event.data.tod}
                month={event.data.month}
                year={event.data.year}
              name={event.name}
              />
            ))
}
          {state.events &&
            state.events.map((item, index) => (
              <Event
                title={item.title}
                day={item.day}
                hour={item.hour}
                minute={item.minute}
                tod={item.tod}
                month={item.month}
                year={item.year}
                index={index}
                publicity={item.public}
                onDelete={() => {
                  deleteEvent(index);
                
                }}
              />
            ))}
        </div>
      </motion.div>
    </React.Fragment>
  );
};
export default Home;
