import React, { useEffect, useState, } from 'react';
import './App.css';
import './calendar.css';
import Login from './components/Login';
import Home from './components/Home';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import Createevent from './components/CreateEvent';
import './Event.css';
import axios from 'axios';
import Friends from "./components/Friends"
import "./Friends.css"
import BetaFooter from "./components/BetaFooter"
//use via props vs via a freq called func becasue it will give better performance!

const App = () => {
  //create a function here that will create the props data. if it cannot find the data for the prop, redirect and/or return null.
  const [state, setState] = useState({})
  //get to removeing the constructors!!!
  useEffect(()=>{
 const cookie = localStorage.getItem("token")
  if (cookie) {
    if(window.location.pathname==="/") {window.location.pathname="/home"}
    axios
      .get('https://calendar-app.systems/getuser', {
        headers: { Authorization: `Bearer ${cookie}` },
      })
      .then((res) => {
        const { events } = res.data.user;
        //console.log(events, '=events');
        var parseddata = [];
        events.forEach((element) => {
          parseddata.push(JSON.parse(element));
        });
        res.data.user.events = parseddata;
        //console.log(res.data);
        const date = new Date()
        setState({
          data: res.data.user.username,
          events: res.data.user.events,
          friendEvents: res.data.friendEvents,
          /*--------------------*/
          friendReqs:res.data.user.friendReqs,
          friends:res.data.user.friends,
          /*--------------------*/ 
          month: date.getMonth(),
          year: date.getFullYear()
        });
      })
      .catch(() => {
        //if the cookie is bad, then login
        //console.log("in catch block, fatal req error")
        localStorage.removeItem("token")
        window.location.pathname = '/';
      });
      //this should ONLY be triggered when the cookie is null and the pathname is not login, so a user is not found.
  }else if(window.location.pathname != "/") {
    //if there is no cookie, the redirect will go through, but the component will handle the rejection.
    //console.log('error!');
    window.location.pathname = '/';
  }
  },[])


    return (
            
      <BrowserRouter>
      {/*to use the props.history.push in react router, make a component for api calls and wrap the switch in it!*/}
        <Switch>
          <Route path="/" exact component={Login} />
          <Route
            path="/home"
            exact
            render={() => (
              <Home year={state.year} username={state.data} friendEvents={state.friendEvents} month={state.month} events={state.events} />
            )}
          />
          <Route exact path="/home/:day/:month/:year" component={Createevent} />
          <Route exact path="/friends" render={()=><Friends friendReqs={state.friendReqs} friends={state.friends}/>} />
          <Route render={() => <h1>404 error</h1>} />
        </Switch>
        <BetaFooter/>
      </BrowserRouter>
      //ok, so right here, I can add a react fragment, and it will still be a part of the App component. I can then add the redirects based on a lack of tokens.
    );
}
export default App;
