import React from 'react';
import axios from 'axios';
import { Redirect } from 'react-router';
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';
const months = [
  'January',
  'February',
  'March',
  'April',
  'May',
  'June',
  'July',
  'August',
  'September',
  'October',
  'November',
  'December',
];
const CreateEvent = (props) => {
  const formatYear = () => {
    const date = new Date();
    if (date.getFullYear() == props.match.params.year) {
      return '';
    } else {
      return props.match.params.year;
    }
  };
  const submitEvent = (hour, minute, tod, day, month, year, title, privacy) => {
    axios
      .post(
        'https://calendar-app.systems/createevent',
        {
          hour: hour,
          minute: minute,
          tod: tod,
          title: title,
          day: day,
          month: month,
          year: year,
          public: privacy,
        },
        {
          headers: {
            Authorization: 'Bearer ' + localStorage.getItem('token'),
          },
        }
      )
      .then(() => {
        window.location.href = '/home';
      });
  };

  const validateOptions = () => {
    const title = document.getElementById('idk').value;
    const hour = document.getElementById('hour').selectedIndex;
    const mins = document.getElementById('mins').selectedIndex;
    const tod = document.getElementById('tod').selectedIndex;
    const privacy =
      document.getElementById('privacy').checked
    if (hour == 0 || mins == 0 || tod == 0) {
      document.getElementById('error').style.color = 'red';
      document.getElementById('error').innerHTML = 'please enter a valid time';
    } else if (title.length < 5) {
      document.getElementById('error').style.color = 'red';
      document.getElementById('error').innerHTML =
        'your event name must be greater than 4 characters';
    } else if (title.length >= 60) {
      document.getElementById('error').style.color = 'red';
      document.getElementById('error').innerHTML =
        'your event name must be less than 60 characters';
    } else {
      submitEvent(
        hour,
        mins - 1,
        tod,
        props.match.params.day,
        props.match.params.month,
        props.match.params.year,
        title,
        privacy
      );
      window.location.href = '/home';
    }
  };
  return (
    <React.Fragment>
      <div className="px-5 pt-5">
        <div className="px-4">
          <h1 id="curdate">
            Scheduling event for {months[props.match.params.month]}{' '}
            {props.match.params.day} {formatYear()}
          </h1>
          <div id="selectors">
            <select id="hour" name="hour">
              <option>hour</option>
              {[...Array(12).keys()].map((item) => (
                <option value={item} key={item++}>
                  {item}
                </option>
              ))}
            </select>
            :
            <select id="mins" name="mins">
              <option>minute</option>
              {[...Array(10).keys()].map((item) => (
                <option key={item}>{'0' + item}</option>
              ))}
              {[...Array(50).keys()].map((item) => (
                <option key={item++}>{9 + item++}</option>
              ))}
            </select>
            :
            <select id="tod" name="tod">
              <option selected>AM/PM</option>
              <option>AM</option>
              <option>PM</option>
            </select>
            &nbsp;&nbsp;
            <span className=" bg-secondary px-2 py-1 rounded radius-5">
              <label for="privacy">public?</label>&nbsp;
              <motion.input
                whileHover={{
                  scale: 1.2,
                }}
                whileTap={{
                  scale: 0.9,
                }}
                type="checkbox"
                name="privacy"
                id="privacy"
                value="public"
              ></motion.input>
            </span>
            <br />
          </div>
          <br />
          <input id="idk" className="form-control" />
          <a id="error"></a>
          <br />

          <motion.button
            whileHover={{
              scale: 1.2,
            }}
            whileTap={{
              scale: 0.9,
            }}
            onClick={validateOptions}
            className="btn btn-primary btn-small"
          >
            Create event
          </motion.button>

          <br />
          <br />
          <Link to="/home">
            <motion.button
              whileHover={{
                scale: 1.2,
              }}
              whileTap={{
                scale: 0.9,
              }}
              className="btn btn-sm btn-secondary"
            >
              &#10094; home
            </motion.button>
          </Link>
        </div>
      </div>
    </React.Fragment>
  );
};

export default CreateEvent;
