import React, { Fragment, useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';
const Addfriend = (props) => {
  const sendReq = (user) => {
    const cookie = localStorage.getItem('token');
    axios
      .post(
        'https://calendar-app.systems/reqfriend',
        {
          user: user,
        },
        { headers: { Authorization: `Bearer ${cookie}` } }
      )
      .then((res) => {
        const getStatus = () => {
          switch (res.status) {
            case 202:
              return user + ' has been added to your friends list!';
            default:
              return 'Friend request sent!';
          }
        };
        document.getElementById('error').style.color = 'green';
        document.getElementById('error').innerText = getStatus();
        //later add a switch so that you can account for friend requests that were already accpeted.
        //ACTUALLY just make friend request acceoting the same as requesting!
      })
      .catch((err) => {

        const getStatus = () => {
          switch (err.response.status) {
            case 500:
              return 'Internal server error, please try again later!';
            case 400:
              return 'User ' + user + ' does not exsist!';
            case 403:
              return 'You have already requested this user!';
            case 409:
              return 'You are already friends with this user!';
            case 423:
              return 'You cannot request yourself!';
            default:
              return 'Unknown error!';
          }
        };
        document.getElementById('error').style.color = 'red';
        document.getElementById('error').innerText = getStatus();
      });
  };

  return (
    <Fragment>
      <div className="px-5 pt-5">
        <div className="px-4">
          <h1 id="curdate">Add a friend</h1>
          <br />
          <input id="idk" className="form-control" />
          <a id="error"></a>
          <br />

          <motion.button
            whileHover={{
              scale: 1.2,
            }}
            whileTap={{
              scale: 0.9,
            }}
            className="btn btn-primary btn-small"
            onClick={() =>
              sendReq(document.getElementById('idk').value.toLowerCase())
            }
          >
            Send Request
          </motion.button>

          <br />
          <p id="error"></p>
          <br />
          <Link to="/home">
            <motion.button
              whileHover={{
                scale: 1.2,
              }}
              whileTap={{
                scale: 0.9,
              }}
              className="btn btn-sm btn-secondary"
            >
              &#10094; home
            </motion.button>
          </Link>
        </div>
      </div>
    </Fragment>
  );
};
export default Addfriend;
