import React, { useEffect, useState, Fragment } from 'react';
import Addfriend from './Addfriend';
import Friendreqs from './Friendreqs';
import axios from 'axios';
import Friend from "./Friend"
import { motion } from 'framer-motion';
const Friends = (props) => {

  const [state, setState] = useState({
    reqs: props.friendReqs,
    friends: props.friends,
  });
  const variants = {
    hidden: { opacity: 0 },
    visible: { opacity: 1 },
  }
  useEffect(() => {
    setState({
      reqs: props.friendReqs,
      friends: props.friends,
    });
  }, [props]);
  const deleteReq = (index) => {
    //add server support.
    const cookie = localStorage.getItem('token');
    if(cookie)
    axios
      .delete('https://calendar-app.systems/deletereq/' + index, {
        headers: { Authorization: 'Bearer ' + cookie },
      })
      .then(() => {
        const newState = {...state};
        newState.reqs.splice(index, 1);
        setState(newState);
      }).catch(()=>{
        return 0
      })
  };
  const acceptReq = (user) => {
    //console.log(user)
    //console.log("working?")
    const cookie = localStorage.getItem("token")
    axios
      .post(
        'https://calendar-app.systems/reqfriend',
        {
          user: user,
        },
        { headers: { Authorization: `Bearer ${cookie}` } }
      )
      .then(()=>{
        const newState = {...state}
        const index = newState.reqs.indexOf(user)
        newState.reqs.splice(index,1)
        newState.friends.push(user)
        setState(newState)
      }).catch(err=>{
        //console.log(err)
        return null
      })
  };
  const deleteFriend = (index) => {
    //add server support.
    const cookie = localStorage.getItem('token');
    if(cookie)
    axios
      .delete('https://calendar-app.systems/deletefriend/' + index, {
        headers: { Authorization: 'Bearer ' + cookie },
      })
      .then(() => {
        const newState = {...state};
        newState.friends.splice(index, 1);
        setState(newState);
      }).catch(()=>{
        return 0
      })
  };
  //localize friends and reqs
  //use usememo?
  //use add friend for accept, but only the route, dont copy entire func!

  return (
    <Fragment>
      <div className="float">
        <div className="event">
          <Addfriend />
        </div>
        <div className="event">
          <br />
          <br />
            <div>
           {/*add if block for title */}
          <h1 className="pl-4">Incoming friend requests</h1>
          <br />

          {state.reqs &&
            state.reqs.map((item, index) => (
              <motion.div
              transition={{ duration: 2 }}
              initial="hidden"
              animate="visible"
              variants={variants}
            >
              <Friendreqs delete={()=>deleteReq(index)} accept={()=>acceptReq(item)} person={item} index={index} />
              </motion.div>
            ))}
            
            </div>
            <div>
              {/*add if block for title */}
            <h1 className="pl-4">Friends</h1>
            
            {state.friends &&
            state.friends.map((item, index) => (
              <motion.div
    transition={{ duration: 2 }}
    initial="hidden"
    animate="visible"
    variants={variants}
  >
              <Friend delete={()=>deleteFriend(index)}person={item} index={index} />
              </motion.div>
            ))}
            <motion.div>
    </motion.div>
            </div>
            </div>
        </div>
    </Fragment>
  );
};
export default Friends;
